<template>
  <div class="wrapper">
    <typeChange @change="changeType"></typeChange>
    <div class="content">
      <div class="c-left">
        <div class='c-l-top'>
          <ctitle :title="`${curCity==='广东省'?'全省':curCity}绩效考核`"></ctitle>
          <div class='c-top-content'>
            <gcgcjkDiv></gcgcjkDiv>
          </div>
        </div>
        <div class='c-l-bottom'>
          <div class='charts'>
            <!-- <radar class="chart" :params="zyzbRadarParams"></radar> -->
            <p>总体</p>
            <div class='all'>
              <blfsList :data="blfsListData" @openListClick="openList"></blfsList>
            </div>
          </div>
          <div class='charts'>
            <p>信访部门</p>
            <div class='all'>
              <blfsList :data="xfbmListData" @openListClick="openList"></blfsList>
            </div>
          </div>
          <div class='charts'>
            <p>责任单位</p>
            <div class='all'>
              <blfsList :data="zrdwListData" @openListClick="openList"></blfsList>
            </div>
          </div>
        </div>

      </div>
      <div class="c-right">
        <ctitle :title="'按期办结率区划分布'"></ctitle>
        <div class="part">
          <!-- <div class="w-radio">
            <el-radio
              v-for="(item,index) of radioArr"
              :key="index"
              v-model="radio"
              :label="item"
              @change="handleRadio"
            >
              {{item}}
            </el-radio>
          </div> -->
          <div class="charts">
            <div v-if="mapParams.options.JSON!=='440000'" class="map-back" @click="mapBack"></div>
            <transition name="fade">
              <Map2 v-if="mapFlag" :params="mapParams" @change="handleMap"></Map2>
            </transition>
          </div>
          <div class='pointQhfb'>
            <qhfbbox :params="dataListParams"></qhfbbox>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import typeChange from "@/components/typeChange/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import loopPie from "@/components/pie/LoopPie";
  import barline from "@/components/bar/BarLine";
  import Map2 from "@/components/map/Map2";
  import radar from "@/components/radar/radar";
  import detailList from "@/components/list/detailList";
  import gcgcjkDiv from "@/components/gcgcjk/index";
  import qhfbbox from "@/components/zdqh/index";
  import blfsList from "@/components/list/allList";

  export default {
    name: "qhfb",
    components: {
      ctitle,
      typeChange,
      PieChart3D,
      loopPie,
      barline,
      Map2,
      radar,
      detailList,
      gcgcjkDiv,
      qhfbbox,
      blfsList
    },
    data() {
      return {
        radioArr:['按期办结率','网信占比','参评率'],
        radio:'按期办结率',
        curCity: "广东省",
        jsjxkhNum:156,
        loadFlag: false,
        popShow: false,
        shijCity: "",
        shej_02: "invalid",
        shij_02: "invalid",
        xj_02: "invalid",
        nameP: "",
        mapJson: {
          "广东省": "440000",
          "广州市": "440100",
          "韶关市": "440200",
          "深圳市": "440300",
          "珠海市": "440400",
          "汕头市": "440500",
          "佛山市": "440600",
          "江门市": "440700",
          "湛江市": "440800",
          "茂名市": "440900",
          "肇庆市": "441200",
          "惠州市": "441300",
          "梅州市": "441400",
          "汕尾市": "441500",
          "河源市": "441600",
          "阳江市": "441700",
          "清远市": "441800",
          "东莞市": "441900",
          "中山市": "442000",
          "潮州市": "445100",
          "揭阳市": "445200",
          "云浮市": "445300",
        },
        //雷达图
        blfsListData: [],
        xfbmListData:[],
        zrdwListData:[],
        //4.地图
        mapFlag: false,
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "100%",
          },
          data: [],
          options: {
            isHome: true,
            JSON: "440000",
            xName: "name",
            yName: "value_g1_1",
            map: {
              "value_g1_1": "办结率",
              "value_g1_2": "已办结",
              "value_g1_3": "超期未办结",
            },
            carousel: true
          }
        },
        // 地图右下角
        dataListParams:{
          zdqhName:'网信占比',
          data:[]
        },
        curTypeId:'drop',
        // 各市切换列表
        menuShow: false,
        menuIndex: 0,
        menuName: "石家庄市",
        menuArr: ["石家庄市", "张家口市", "保定市", "秦皇岛市", "廊坊市", "衡水市", "邯郸市", "承德市", "邢台市", "唐山市", "沧州市", "定州市", "辛集市", "雄安新区"],
        detailList: null,
        // 四级登记机构占比
        djjgPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["60%", "75%"],
            center: ["50%", "50%"],
            centerRadius: ["52%", "53%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 45,
            borderWidth: this.fontSize(0.05),
            bRichPadding: [30, -45, 0, -45],
            tooltip: {
              show: true
            },
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "14%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        },
        cityList:[]
        
      };
    },
    mounted() {
      this.init(this.date);
    },
    methods: {
      init(date){
        this.getAPI(date);
        this.getMapAPI(date);
        // this.dataListShow()
        // this.handleRadio()
      },
      // 头部类型切换
      changeType(id) {
        this.curTypeId = id;
        this.getAPI(this.curTypeId);
        this.getMapAPI(this.curTypeId);
        this.openList(this.name,this.curTypeId)
      },
      handlePopShow() {
        this.popShow = true;
      },
      openList(name,curTypeId){
        get_http("zrdwhxQstzMap", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
          day: `[${this.date.startDate},${this.date.endDate}]`
        })
          .then(res => {
            this.mapParams.options.map.value=name;
            this.mapParams.data = res.data.data;
            this.dataListShow(name)
            this.handleMap(name)
            this.getAPI(name)
            this.getMapAPI(name)
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 2.办理方式
      getAPI(date,curTypeId) {
        get_http("xfjxkhqhfballRadar", {
          day: `[${date.startDate},${date.endDate}]`
          })
          .then(res => {
            this.blfsListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          })
        get_http("xfjxkhqhfbxfbmRadar", {
          day: `[${date.startDate},${date.endDate}]`
          })
          .then(res => {
            this.xfbmListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        get_http("xfjxkhqhfbzrdwRadar", {
          day: `[${date.startDate},${date.endDate}]`
          })
          .then(res => {
            this.zrdwListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 3.全省信访量-地图
      getMapAPI(date,name,curTypeId) {
        // 处理接口参数
        // 省级
        if (this.curCity === '广东省') {
          this.shej_02 = this.curCity;
          
          this.shij_02 = 'invalid';
          this.xj_02 = 'invalid';
          this.nameP = 'shij_02';
          
        } else {
          // 市级
          if (this.shijCity) {
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = 'invalid';
            this.nameP = 'xj_02';
          } else {
            // 县区级
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = this.curCity;
            this.nameP = 'invalid';
          }
        }
        get_http("xfjxkhqhfbaqbjlqvfb", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
          id:this.curTypeId,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.mapParams.options.map = res.data.map;
            this.mapParams.data = res.data.data;
            this.mapFlag = true;
            this.dataListParams.data=this.mapParams.data
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击地图下钻
      handleMap(name) {
        this.curCity = name;
        for (let key in this.mapJson) {
          if (this.curCity === key) {
            this.mapFlag = false;
            this.mapParams.options.JSON = this.mapJson[name];
          }
        }
      },
      // 点击单选框
      /*handleRadio(name){
        // 这里name后续需给接口传参
        get_http("zrdwhxQstzMap", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
          day: `[${this.date.startDate},${this.date.endDate}]`
        })
          .then(res => {
            this.mapParams.options.map.value=name;
            this.mapParams.data = res.data.data;
            this.dataListShow(name)
            this.handleMap(name)
            this.getAPI(name)
            this.getMapAPI(name)
            
            
          })
          .catch(err => {
            console.log(err);
          });
      },*/
      // 重点关注区划
      dataListShow(name){
        // 这里name后续需给接口传参
        get_http("xfjxkhqhfbaqbjlqvfb", {
          // shej_02: this.shej_02,
          // shij_02: this.shij_02,
          // xj_02: this.xj_02,
          name: this.nameP,
          // day: `[${this.date.startDate},${this.date.endDate}]`
        })
          .then(res => {
            this.dataListParams.zdqhName=name;
            this.dataListParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 地图返回全省
      mapBack() {
        this.mapFlag = false;
        this.curCity = "广东省";
        this.mapParams.options.JSON = "440000";
      },
      computation() {
        let height = (60 * this.leftBarParams.data.length) / 100 + "rem";
        $(this.$refs.barEl).css('height', height);
        this.initScroll();
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      },
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      curCity: function (newVal, oldVal) {
        this.curReci = "drop";
        if (newVal === '广东省') {
          this.getMapAPI(this.date);
        } else {
          let c = '';
          for (let key in this.mapJson) {
            if (newVal === key) {
              this.shijCity = newVal;
              c = newVal;
              this.getMapAPI(this.date);
            }
          }
          if (!c) {
            this.xj_02 = newVal;
            this.getAPI(this.date)
          }
        }
      },
      date(val) {
        this.getAPI(val);
        this.getMapAPI(val);
      },
      year(val) {
        this.getAPI(val);
      }
      
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper /deep/ .el-radio__label
    font-size .18rem

  .wrapper /deep/ .el-radio__input.is-checked + .el-radio__label
    color #f996d6
    cursor pointer

  .wrapper /deep/ .el-radio__input.is-checked .el-radio__inner
    border-color #f996d6
    background-color #f996d6

  .wrapper /deep/ .el-radio
    color #cbfbff
    display block
    line-height .35rem
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .fade-enter-active, .fade-leave-active
      transition opacity .3s
    .fade-enter, .fade-leave-to
      opacity 0
    .w-seach
      position absolute
      top -78px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 262px
      line-height 62px
      font-size 22px
      color #cbfbff
      background url("../../../assets/seach-bg.png") no-repeat
      cursor pointer
      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
    
    .content
      width 100%
      height 100%
      display flex
      flex-direction row
      padding .2rem .3rem
      box-sizing border-box

      .c-left 
        flex .9
        position relative
        box-sizing border-box
        flex-direction column
        display flex
        .c-l-top 
          flex .6
          box-sizing border-box
          display flex
          flex-direction column
          .c-top-content
            display flex
            flex .8
        .c-l-bottom
          flex 1
          display flex
          flex-direction column
          .charts
            flex 1
            display flex
            flex-direction row
            align-items center
            p
              flex .2
              font-size 16px
              color #d0f0fb
              text-align center
              padding-left .6rem
            .all
              flex 1.2
              .blfs-list
                height 106px
      .c-right
        flex 1.1
        position relative
        box-sizing border-box
        flex-direction column
        justify-content center
        display flex
        .part
          padding-bottom .1rem
          flex 4
          display flex
          flex-direction row
          width 100%
          height 100%
          position relative
          .pointQhfb
            
            position absolute
            
            
            bottom  .5rem
            right 1.2rem
          .w-radio
            position absolute
            top .2rem
            left .7rem
            z-index 10
          .charts
            position relative
            flex 1
            width 100%
            height 100%
            
          .fade-enter-active, .fade-leave-active
            transform scale(1)
            transition all 0.8s

          .fade-enter, .fade-leave-to
            opacity 0
            transform scale(0)

          .map-back
            position absolute
            top 30px
            right 60px
            z-index 1000
            width 30px
            height 30px
            background url("~@/assets/back-icon.png") no-repeat
            cursor pointer

</style>
